import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes} from '@angular/router';
import { MobPartsComponent } from './mob-parts/mob-parts.component';
import { BooksComponent } from './books/books.component';
import { CloothsComponent } from './clooths/clooths.component';
import { ElectronicsComponent } from './electronics/electronics.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { HomePageComponent } from './home-page/home-page.component';

const appRoute : Routes = [
	{ path : 'mobile', component: MobPartsComponent },
	{ path : 'venky', component: BooksComponent },
	{ path : 'ele', component: ElectronicsComponent },
	{ path : 'cloths', component: CloothsComponent },
	{ path : 'home', component: HomePageComponent },
	{ path : '', component: HomePageComponent },

	{ path : '**', component: PageNotFoundComponent }
]
@NgModule({
  imports: [CommonModule,RouterModule.forRoot(appRoute)],
  exports: [RouterModule]
})
export class DummyModuleModule { }
