import { Component, OnInit } from '@angular/core';
import { BOOK } from './mocks';
import { BooksDataTypes } from './BooksDataTypes'; 
@Component({
  selector: 'app-books',
  templateUrl: './books.component.html',
  styleUrls: ['./books.component.css']
})
export class BooksComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  	this.Books = BOOK; 
  }
  Books : BooksDataTypes[];
	calc()
	{
		let add=0;
		for(let temp of this.Books)
		{
			add = add + temp.Stock;
		}
		return add;
	}

}

