import { Component, OnInit } from '@angular/core';
import { CloothsDataTypes } from './CloothsDataTypes';
import { CLOOTH } from './mocks';
@Component({
  selector: 'app-clooths',
  templateUrl: './clooths.component.html',
  styleUrls: ['./clooths.component.css']
})
export class CloothsComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  	this.Clooths = CLOOTH;
  }
  Clooths : CloothsDataTypes[];
calc()
	{
		let add=0;
		for(let temp of this.Clooths)
		{
			add = add + temp.Stock;
		}
		return add;
	}
}
